const rows = [];
const columns = [
  { field: 'num', headerName: ' /', width: 70 },
  { field: 'name', headerName: '', width: 300 },
  { field: 'family', headerName: '', width: 300 },
  { field: 'emails', headerName: 'e-mail', width: 250 },
  { field: 'telNumbers', headerName: '', width: 200 },
]

for (let i = 0; i < contacts.length; i++) {
  rows[i] = { id: i + 1, num: i + 1, name: contacts[i].name, family: contacts[i].family };
  rows[i].emails = [];
  rows[i].telNumbers = [];
  if (contacts[i].emails) {
    for (let j = 0; j < contacts[i].emails.length; j++) {
      rows[i].emails.push(contacts[i].emails[j].data);
    };
  } else {
    rows[i].emails.push("");
  }
  if (contacts[i].telNumbers) {
    for (let j = 0; j < contacts[i].telNumbers.length; j++) {
      rows[i].telNumbers.push(contacts[i].telNumbers[j].data);
    };
  } else {
    rows[i].telNumbers.push("");
  }
}

function App() {
  return (
    <div className="App">
      <div className="main_wrapper">
        <h1>    </h1>
        <div className="table">
          <DataGrid
            hideFooterSelectedRowCount
            autoPageSize
            rows={rows}
            columns={columns}
            rowsPerPageOptions={[10]}
          />
        </div>
        <h4 className='tag'>designed by Stranger</h4>
      </div>
    </div>
  );
}

export default App;
